<template>
    <div class="login" v-loading="true">

    </div>
</template>

<script>
    export default {
        name: "login",
        created() {
            this.checkLogin();
        },
        methods: {
            checkLogin() {
                const _self = this;
                _self.$accessToken(null);
                _self.$api('getUserToken', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.$accessToken(reData.data.token);
                        if (_self.$route.query.hasOwnProperty('redirect') && _self.$route.query.redirect!=='') {
                            if(_self.$route.query.redirect.indexOf('http') !== 0 && _self.$route.query.redirect.indexOf('//') !== 0){
                                _self.$router.push(_self.$route.query.redirect);
                            }else{
                                window.location.href = _self.$route.query.redirect;
                            }
                        } else {
                            _self.$router.push({name: 'home'});
                        }
                    } else {
                        _self.goLogin();
                    }
                }, function (error) {
                    console.log(error);
                    _self.goLogin();
                }, false, false);
            },
            goLogin() {
                window.location.href = this.$C('serverUrlAjax', 'sys') + '/exusers/u8_login.php?idweb='+this.$getWebId()+'&lang='+this.$langID()+'&gourl=' + encodeURIComponent(window.location.href);
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../assets/css/common/lib.scss';

    .login {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
